
'use client';

import * as React from 'react';
import type { Session, User, Notification } from '@/lib/types';
import { useAuth } from './auth-context';
import { useFirebase, useCollection, useMemoFirebase } from '@/firebase';
import { collection, query, where, doc, updateDoc, or } from 'firebase/firestore';
import { errorEmitter } from '@/firebase/error-emitter';
import { FirestorePermissionError } from '@/firebase/errors';

interface NotificationContextType {
    user: User | null;
    sessions: Session[];
    users: User[];
    notifications: Notification[];
    isLoading: boolean;
    isMounted: boolean;
    updateSession: (session: Session) => void;
    markNotificationsAsRead: (sessionId: string, userId: string) => void;
}

const NotificationContext = React.createContext<NotificationContextType | null>(null);

export function NotificationProvider({ children }: { children: React.ReactNode }) {
    const { user, allUsers, isLoading: isAuthLoading } = useAuth();
    const { firestore } = useFirebase();
    const [isMounted, setIsMounted] = React.useState(false);

    React.useEffect(() => {
        setIsMounted(true);
    }, []);

    const userSessionsQuery = useMemoFirebase(() => {
        if (!firestore || !user) return null;
        return query(
            collection(firestore, 'sessions'),
            or(
                where('hostId', '==', user.id),
                where('clientId', '==', user.id)
            )
        );
    }, [firestore, user]);

    const { data: userSessions, isLoading: sessionsLoading } = useCollection<Session>(userSessionsQuery);
    
    const adminSessionsQuery = useMemoFirebase(() => {
        if (!firestore || !user || user.role !== 'admin') return null;
        return collection(firestore, 'sessions');
    }, [firestore, user]);

    const { data: adminSessions, isLoading: adminLoading } = useCollection<Session>(adminSessionsQuery);
    
    const sessions = React.useMemo(() => {
        if (user?.role === 'admin') return adminSessions || [];
        return userSessions || [];
    }, [user, userSessions, adminSessions]);

    const notifications = React.useMemo(() => {
        if (!user || !sessions || sessions.length === 0) return [];
        const newNotifications: Notification[] = [];
        sessions.forEach(session => {
            // Only create notifications for sessions the user is actually a part of
            if (session.hostId !== user.id && session.clientId !== user.id) {
                return;
            }

            if (session.status === 'cancelled' && !(session.cancellationReadBy || []).includes(user.id)) {
                newNotifications.push({
                    id: `${session.id}-cancel`,
                    sessionId: session.id,
                    type: 'cancellation',
                    message: `Session "${session.title}" was cancelled.`,
                    timestamp: session.cancelledAt!,
                });
            }
            const lastMessage = session.messages?.[session.messages.length - 1];
            if (lastMessage && lastMessage.senderId !== user.id && !(session.lastMessageReadBy || []).includes(user.id)) {
                newNotifications.push({
                    id: `${session.id}-message`,
                    sessionId: session.id,
                    type: 'message',
                    message: `New message in "${session.title}"`,
                    timestamp: lastMessage.timestamp,
                });
            }
        });
        return newNotifications.sort((a, b) => new Date(b.timestamp).getTime() - new Date(a.timestamp).getTime());
    }, [sessions, user]);

    const markNotificationsAsRead = React.useCallback(async (sessionId: string, userId: string) => {
        const session = sessions.find(s => s.id === sessionId);
        if (!session || !firestore) return;
        
        const sessionDocRef = doc(firestore, 'sessions', sessionId);
        const updateData: Partial<Session> = {};
        let needsUpdate = false;

        if (session.status === 'cancelled' && !(session.cancellationReadBy || []).includes(userId)) {
            updateData.cancellationReadBy = [...(session.cancellationReadBy || []), userId];
            needsUpdate = true;
        }
        const lastMessage = session.messages?.[session.messages.length - 1];
        if (lastMessage && lastMessage.senderId !== userId && !(session.lastMessageReadBy || []).includes(userId)) {
            updateData.lastMessageReadBy = [...(session.lastMessageReadBy || []), userId];
            needsUpdate = true;
        }

        if (needsUpdate) {
            try {
                await updateDoc(sessionDocRef, updateData);
            } catch (error) {
                errorEmitter.emit('permission-error', new FirestorePermissionError({
                    path: sessionDocRef.path, operation: 'update', requestResourceData: updateData
                }));
            }
        }
    }, [sessions, firestore]);
    
    const updateSession = async (session: Session) => {
        if (!firestore) return;
        const sessionDocRef = doc(firestore, 'sessions', session.id);
        try {
            await updateDoc(sessionDocRef, { ...session });
        } catch (error) {
            errorEmitter.emit('permission-error', new FirestorePermissionError({
                path: sessionDocRef.path,
                operation: 'update',
                requestResourceData: session,
            }));
        }
    };

    const isLoading = user?.role === 'admin' 
        ? adminLoading || isAuthLoading 
        : sessionsLoading || isAuthLoading;

    const value = {
        user,
        sessions,
        users: allUsers,
        notifications,
        isLoading,
        isMounted,
        updateSession,
        markNotificationsAsRead,
    };

    return (
        <NotificationContext.Provider value={value}>
            {children}
        </NotificationContext.Provider>
    );
}

export function useNotifications() {
    const context = React.useContext(NotificationContext);
    if (!context) {
        throw new Error('useNotifications must be used within a NotificationProvider');
    }
    return context;
}
