
"use client";

import type { User } from '@/lib/types';
import { useRouter } from 'next/navigation';
import * as React from 'react';
import { useFirebase, useUser } from '@/firebase';
import { signOut, signInAnonymously } from 'firebase/auth';

interface AuthContextType {
  user: User | null;
  allUsers: User[];
  isLoading: boolean;
  login: (user: User) => void;
  logout: () => void;
}

const AuthContext = React.createContext<AuthContextType | null>(null);

const CURRENT_USER_STORAGE_KEY = 'work-session-user';

export function AuthProvider({ children }: { children: React.ReactNode }) {
  const [user, setUser] = React.useState<User | null>(null);
  const [isLoading, setIsLoading] = React.useState(true);
  const router = useRouter();
  const { isUserLoading: isFirebaseUserLoading, user: firebaseUser } = useUser();
  const { auth } = useFirebase();

  // This is the correct way to get all users, by providing a static list.
  const allUsers: User[] = [
      { id: '1', name: 'Admin Adam', username: 'admin', role: 'admin', avatarUrl: '' },
      { id: '2', name: 'Varsity Val', username: 'varsity', role: 'varsity', avatarUrl: '' },
      { id: '3', name: 'Novice Nick', username: 'novice', role: 'novice', avatarUrl: '' },
      { id: '4', name: 'Varsity Vera', username: 'vera', role: 'varsity', avatarUrl: '' },
      { id: '5', name: 'Novice Nate', username: 'nate', role: 'novice', avatarUrl: '' },
      { id: '6', name: 'Mr. Willeby', username: 'Mr.Willeby', role: 'admin', avatarUrl: '' },
  ];
  
  React.useEffect(() => {
    const storedUser = localStorage.getItem(CURRENT_USER_STORAGE_KEY);
    if (storedUser) {
        try {
            const parsedUser = JSON.parse(storedUser);
            // Ensure the user from storage has a Firebase UID
            const userWithUid = { ...parsedUser, id: firebaseUser?.uid || parsedUser.id };
            setUser(userWithUid);

        } catch (e) {
            localStorage.removeItem(CURRENT_USER_STORAGE_KEY);
        }
    }
    setIsLoading(false); 
  }, []);

  const login = async (userToLogin: User) => {
    const foundUser = allUsers.find(u => u.username.toLowerCase() === userToLogin.username.toLowerCase());

    if (foundUser) {
        if (auth && !auth.currentUser) {
            try {
                const userCredential = await signInAnonymously(auth);
                const userWithUid = { ...foundUser, id: userCredential.user.uid };
                setUser(userWithUid);
                localStorage.setItem(CURRENT_USER_STORAGE_KEY, JSON.stringify(userWithUid));
                router.push('/dashboard/welcome');
            } catch (error) {
                 console.error("Anonymous sign-in failed", error);
                 // Fallback to login without UID if anon sign-in fails
                 setUser(foundUser);
                 localStorage.setItem(CURRENT_USER_STORAGE_KEY, JSON.stringify(foundUser));
                 router.push('/dashboard/welcome');
            }
        } else if (auth?.currentUser) {
             const userWithUid = { ...foundUser, id: auth.currentUser.uid };
             setUser(userWithUid);
             localStorage.setItem(CURRENT_USER_STORAGE_KEY, JSON.stringify(userWithUid));
             router.push('/dashboard/welcome');
        } else {
             // Fallback for when auth is not ready
             setUser(foundUser);
             localStorage.setItem(CURRENT_USER_STORAGE_KEY, JSON.stringify(foundUser));
             router.push('/dashboard/welcome');
        }
    } else {
        throw new Error('User not found. Try one of: admin, varsity, novice, Mr.Willeby');
    }
  };

  const logout = async () => {
    if (!auth) return;
    await signOut(auth);
    setUser(null);
    localStorage.removeItem(CURRENT_USER_STORAGE_KEY);
    window.location.href = '/';
  };

  const value = { user, allUsers, isLoading, login, logout };

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  );
}

export function useAuth() {
  const context = React.useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
}
